:root {
  --ff: "Outfit", sans-serif;
}
@media (min-width: 43.75em) {
  :root {
    --fs: 1.25rem;
  }
}

:root {
  --Black: hsl(0, 0%, 11%);
  --Dark-Blue: hsl(215, 28%, 17%);
  --Dark-Blue-Light: hsl(215, 30%, 22%);
  --Line: hsl(215, 28%, 25%);
  --White: hsl(0, 0%, 100%);
  --Green: hsl(144, 70%, 58%);
  --Light-Green: hsl(160, 84%, 39%);
  --Medium-Grey: hsl(255, 5%, 84%);
}

.row {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
}
@media (min-width: 43.75em) {
  .row {
    flex-direction: row;
  }
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

body {
  width: 90%;
  margin-inline: auto;
  margin-block: 4rem;
  height: calc(100vh - 8rem);
  display: grid;
  place-content: center;
  background-color: var(--Black);
  line-height: 1.5;
}

h1 {
  line-height: 1.1;
}

button {
  border: none;
  font-family: inherit;
}

body {
  font-family: var(--ff);
  font-size: var(--fs);
}

.card {
  color: var(--White);
}

.green {
  color: var(--Green);
}

.card {
  background-color: var(--Dark-Blue);
  padding-block: min(25vh, 7rem);
  padding-inline: min(10vh, 3.5rem);
}
.card__button {
  display: block;
  margin-inline: auto;
  padding: 0.75em 1.5em;
  background-color: var(--Green);
  border-radius: 0.375rem;
  margin-bottom: 5em;
  transition: opacity 0.5s ease;
}
.card__button:hover {
  opacity: 0.7;
}
.card__title {
  margin-bottom: 0.25em;
}
.card__text {
  margin-bottom: 2.5em;
}
.card__box {
  background-color: var(--Dark-Blue-Light);
  width: 13rem;
  aspect-ratio: 5/1;
  border-radius: 0.375rem;
}

.line {
  height: 1px;
  background-color: var(--Line);
  margin-bottom: 2rem;
}